.App {
  text-align: center;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.react-multi-carousel-item{
  display: flex;
  justify-content: center;
}

#carrousel{

  background-color: #D7D6DE;
  padding: 0 0 2vh 0;
  margin: 30px 20px;
  border-radius: 5px;
}

#titlenovedades{
  text-align: left;
  padding: 0 0 10px 20px;
  font-weight: bolder;
  font-size: 20px;
  font-family: roboto;
  color: rgb(36 24 65)
}

.footer{
  background-color: #D7D6DE;
}

.popup{
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(20, 20, 20, 0.8);
    
    z-index: 100000;
    display: flex;
    justify-content: center;
    align-items: center;


}

.popup-inner{
    position: relative;
    padding: 32px;
    width: 100%;
    max-width: 640px;
    background-color: white;

}

.popup-inner .close-btn{
    position: absolute;
    top: 16px;
    right: 16px;
}
